import React from "react";

const L = () => (
    <button class="bg-orange-600 rounded-full text-white flex py-2 px-4 m-2 relative mx-auto hover:bg-orange-700 transition-colors">
        <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            stroke-width="2"
            stroke="currentColor"
            class="w-6 h-6 mr-1.5"
        >
            <path
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M5.25 5.653c0-.856.917-1.398 1.667-.986l11.54 6.348a1.125 1.125 0 010 1.971l-11.54 6.347a1.125 1.125 0 01-1.667-.985V5.653z"
            />
        </svg>

        <p class="hover:animate-pulse font-medium tracking-wide">Play</p>
    </button>
);

export default L;
